<template>
  <section class="playpage" v-if="songItem.song">
    <div
      class="playpage-mark"
      :style="{
        backgroundImage: `url(${songItem.picUrl}?imageView=1&type=webp&thumbnail=360x0)`,
      }"
    ></div>
    <ul class="pagetop">
      <li class="one">
        <div class="arname">
          {{ songItem.name }}
          <span v-for="n in songItem.song.alias" :key="n">{{ n }}</span>
        </div>

        <span
          class="last"
          v-for="artist in songItem.song.artists"
          :key="artist.id"
          >{{ artist.name }}</span
        >
      </li>
      <li @click="$emit('get-showPlayPage', false)">
        <i class="fa fa-chevron-circle-down"></i>
      </li>
    </ul>

    <PlayPageRecord
      v-if="!showlyric"
      :playstart="playstart"
      :SongPic="SongPic"
      :currentTime="currentTime"
      :duration="duration"
      @play-page-model="$emit('play-page-model', $event)"
      @play-page-back="$emit('play-page-back')"
      @play-main-start="$emit('play-main-start')"
      @play-page-next="$emit('play-page-next')"
      @get-showList="$emit('get-showList', $event)"
      @play-page-change="$emit('play-page-change', $event)"
    ></PlayPageRecord>

    <Lyric
      v-else
      :songItemId="songItemId"
      :currentTime="currentTime"
      :playstart="playstart"
      :duration="duration"
      @get-showList="$emit('get-showList', $event)"
      @play-page-model="$emit('play-page-model', $event)"
      @play-page-back="$emit('play-page-back')"
      @play-main-start="$emit('play-main-start')"
      @play-page-next="$emit('play-page-next')"
      @play-page-change="$emit('play-page-change', $event)"
    ></Lyric>
    <div class="back" @click.stop="showlyric = !showlyric">
      <i :class="[showlyric ? 'fa fa-angle-down' : 'fa fa-angle-up']"></i>
    </div>
  </section>

  <section class="playpage" v-else-if="songItem.artists">
    <div
      class="playpage-mark"
      :style="{
        backgroundImage: `url(${SongPic}?imageView=1&type=webp&thumbnail=360x0)`,
      }"
    ></div>
    <ul class="pagetop">
      <li class="one">
        <div class="arname">
          {{ songItem.name }}
          <span v-for="n in songItem.alias" :key="n">{{ n }}</span>
        </div>

        <span
          class="last"
          v-for="artist in songItem.artists"
          :key="artist.id"
          >{{ artist.name }}</span
        >
      </li>
      <li @click="$emit('get-showPlayPage', false)">
        <i class="fa fa-chevron-circle-down"></i>
      </li>
    </ul>

    <PlayPageRecord
      v-if="!showlyric"
      :playstart="playstart"
      :SongPic="SongPic"
      :currentTime="currentTime"
      :duration="duration"
      @play-page-model="$emit('play-page-model', $event)"
      @play-page-back="$emit('play-page-back')"
      @play-main-start="$emit('play-main-start')"
      @play-page-next="$emit('play-page-next')"
      @get-showList="$emit('get-showList', $event)"
      @play-page-change="$emit('play-page-change', $event)"
    ></PlayPageRecord>

    <Lyric
      v-else
      :songItemId="songItemId"
      :currentTime="currentTime"
      :playstart="playstart"
      :duration="duration"
      @get-showList="$emit('get-showList', $event)"
      @play-page-model="$emit('play-page-model', $event)"
      @play-page-back="$emit('play-page-back')"
      @play-main-start="$emit('play-main-start')"
      @play-page-next="$emit('play-page-next')"
      @play-page-change="$emit('play-page-change', $event)"
    ></Lyric>
    <div class="back" @click.stop="showlyric = !showlyric">
      <i :class="[showlyric ? 'fa fa-angle-down' : 'fa fa-angle-up']"></i>
    </div>
  </section>

  <section class="playpage" v-else>
    <div
      class="playpage-mark"
      :style="{
        backgroundImage: `url(${songItem.al.picUrl}?imageView=1&type=webp&thumbnail=360x0)`,
      }"
    ></div>
    <ul class="pagetop">
      <li class="one">
        <div class="arname">
          {{ songItem.name }}
          <span v-for="n in songItem.alia" :key="n">{{ n }}</span>
        </div>

        <span class="last" v-for="artist in songItem.ar" :key="artist.id">{{
          artist.name
        }}</span>
      </li>
      <li @click="$emit('get-showPlayPage', false)">
        <i class="fa fa-chevron-circle-down"></i>
      </li>
    </ul>

    <PlayPageRecord
      v-if="!showlyric"
      :playstart="playstart"
      :SongPic="SongPic"
      :currentTime="currentTime"
      :duration="duration"
      @get-showList="$emit('get-showList', $event)"
      @play-page-model="$emit('play-page-model', $event)"
      @play-page-back="$emit('play-page-back')"
      @play-main-start="$emit('play-main-start')"
      @play-page-next="$emit('play-page-next')"
      @play-page-change="$emit('play-page-change', $event)"
    ></PlayPageRecord>
    <Lyric
      v-else
      :songItemId="songItemId"
      :currentTime="currentTime"
      :playstart="playstart"
      :duration="duration"
      @get-showList="$emit('get-showList', $event)"
      @play-page-model="$emit('play-page-model', $event)"
      @play-page-back="$emit('play-page-back')"
      @play-main-start="$emit('play-main-start')"
      @play-page-next="$emit('play-page-next')"
      @play-page-change="$emit('play-page-change', $event)"
    ></Lyric>
    <div class="back" @click.stop="showlyric = !showlyric">
      <i :class="[showlyric ? 'fa fa-angle-down' : 'fa fa-angle-up']"></i>
    </div>
  </section>
</template>

<script>
import Lyric from "@/components/Lyric.vue";
import PlayPageRecord from "@/components/PlayPageRecord.vue";

export default {
  components: { Lyric, PlayPageRecord },

  props: {
    songItem: { type: Object },
    playstart: { type: Boolean },
    SongPic: { type: String },
    songItemId: { type: Number },
    currentTime: { type: Number },
    duration: { type: Number },
  },
  data: function () {
    return {
      showlyric: false,
    };
  },
};
</script>

<style lang="less" scoped>
.playpage {
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  overflow: hidden;
  z-index: 6;
  i {
    color: white;
    font-size: 26px;
  }
  &::before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-color: rgb(99, 99, 99);
    position: absolute;
    left: 0;
    top: 0;
    z-index: -3;
  }
  .playpage-mark {
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    filter: blur(30px);
    position: absolute;
    transform: scale(1.5);
    z-index: -2;
    transition: all 0.3s;
  }
  ul.pagetop {
    height: 50px;
    width: 100%;
    background-color: rgba(51, 51, 51, 0.3);
    box-shadow: 0 0 5px 0 rgb(43, 43, 43);
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    padding: 0 12px;
    .one {
      width: 80%;
      .arname {
        font-size: 20px;
        text-shadow: 1px 1px rgb(43, 43, 43);
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
        span {
          &::before {
            content: "(";
          }
          &::after {
            content: ")";
          }
        }
      }
      .last {
        font-size: 12px;
        &::after {
          content: "/";
        }
        &:last-of-type {
          &::after {
            content: "";
          }
        }
      }
    }
  }
  .back {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    bottom: 0;
    height: 30px;
    background-color: rgba(155, 155, 155, 0.3);
    width: 100%;
  }
}
</style>