<template>
  <section class="search">
    <div class="searchSon" @scroll="scroll">
      <!-- 搜索框 -->
      <div class="input">
        <div class="inbj">
          <i class="fa fa-search"></i>
          <input
            type="text"
            v-model.trim="value"
            placeholder="搜索歌曲、歌手、专辑"
            @keyup.enter="value && (searchs = true)"
            @focus="focus = true"
            @blur="focus = false"
          />
          <i
            class="fa fa-times-circle"
            @click="
              value = '';
              searchs = false;
            "
          ></i>
        </div>
      </div>
      <!-- 热门 -->
      <div class="hots" v-if="!value && !searchs">
        <h3>热门搜索</h3>
        <ul class="hotslist">
          <li
            v-for="(item, index) in hots"
            :key="index"
            @click="
              searchs = true;
              value = item.first;
            "
          >
            {{ item.first }}
          </li>
        </ul>

        <!-- 历史 -->
        <ul class="history" v-if="history[0]">
          <h3>历史记录</h3>
          <li
            v-for="(item, index) in history"
            :key="index"
            @click="
              searchs = true;
              value = item;
            "
          >
            <i class="fa fa-history"></i>
            <span>{{ item }}</span>
            <i class="fa fa-times-circle" @click.stop="deleteHistory(item)"></i>
          </li>
        </ul>
      </div>
      <!-- 搜索建议 -->
      <div class="suggestions" v-if="value && !searchs">
        <h3>搜索“{{ value }}”</h3>
        <ul>
          <li
            v-for="(item, index) in suggestions"
            :key="index"
            @click="
              searchs = true;
              value = item.keyword;
            "
          >
            <i class="fa fa-search"></i>{{ item.keyword }}
          </li>
        </ul>
      </div>
      <!-- 搜索结果 -->
      <div class="suggestdata" v-if="searchs">
        <SongList
          v-for="(item, index) in suggestData"
          :key="index"
          :item="item"
          :songItemId="songItemId"
          :playstart="playstart"
          @get-song-list="
            $emit('get-song-list', $event);
            $emit('get-home-play', suggestData);
          "
        ></SongList>

        <p v-if="!hasMore">没有更多了</p>
      </div>
    </div>
  </section>
</template>

<script>
import SongList from "@/components/SongList.vue";

export default {
  components: { SongList },
  props: {
    songItemId: {
      type: Number,
    },
    playstart: {
      type: Boolean,
    },
  },

  data: function () {
    return {
      value: "",
      hots: [],
      suggestData: [],
      suggestions: [],
      searchs: false,
      focus: false,
      page: 0,
      hasMore: false,
      history: JSON.parse(window.localStorage.getItem("history")) || [],
      newArr: [],
    };
  },
  created: function () {
    this.axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
      this.hots = res.data.result.hots;
    });
  },
  methods: {
    scroll: function (event) {
      // console.log(event.target.offsetHeight + event.target.scrollTop);
      // console.log(event.target.scrollHeight);
      if (this.hasMore) {
        if (
          Math.round(event.target.offsetHeight + event.target.scrollTop) ===
          event.target.scrollHeight
        ) {
          this.getSearchs();
        }
      }
    },
    getSearchs: function () {
      this.axios
        .get("https://apis.netstart.cn/music/search", {
          params: {
            keywords: this.value,
            limit: 30,
          },
        })
        .then((res) => {
          this.suggestData.push(...res.data.result.songs);
          this.page++;
          this.hasMore = res.data.result.hasMore;
        });

      this.history = [...new Set([...this.history, this.value])];
      // console.log(this.history);
      window.localStorage.setItem("history", JSON.stringify(this.history));
      // for (let i = this.history.length; i > 0; i--) {
      //   this.newArr.push(this.history[i - 1]);
      //   this.newArr = [...new Set(this.newArr)];
      // }
    },
    deleteHistory: function (value) {
      this.history = this.history.filter((item) => item !== value);
      window.localStorage.setItem("history", JSON.stringify(this.history));
    },
    // getTimer: function () {
    //   let timer = 0;
    //   return function (back, m) {
    //     clearTimeout(timer);
    //     timer = setTimeout(back, m);
    //   };
    // },
  },
  watch: {
    value: function (n) {
      if (n) {
        // this.getTimer();
        if (this.focus) {
          this.searchs = false;
        }
        if (n && !this.searchs) {
          this.axios
            .get("https://apis.netstart.cn/music/search/suggest", {
              params: {
                keywords: n,
                type: "mobile",
              },
            })
            .then((res) => {
              this.suggestions = res.data.result.allMatch;
            });
        } else {
          this.suggestions = [];
        }
      }
    },

    searchs: function (n) {
      if (n && this.value) {
        this.getSearchs();
      } else {
        this.suggestData = [];
      }
    },
  },
};
</script>
<style lang="less" scoped>
.search {
  height: calc(100vh - 128px);
  overflow: hidden;
  .searchSon {
    position: absolute;
    height: 100%;
    width: 100%;
    overflow-y: auto;
    padding: 10px;
    i {
      color: #c9c9ca;
      opacity: 0.5;
    }

    .input {
      // padding: 10px;
      padding-bottom: 10px;
      border-bottom: 1px solid rgba(235, 235, 235, 0.7);
      .inbj {
        width: 100%;
        height: 30px;
        padding: 0 30px;
        background: #ebecec;
        border-radius: 30px;
        position: relative;
        .fa-search {
          position: absolute;
          left: 10px;
          top: 7px;
          color: #888;
        }
        .fa-times-circle {
          position: absolute;
          top: 7px;
          right: 6px;
          color: #888;
        }
        input {
          border: none;
          outline: none;
          border-radius: 30px;
          height: 100%;
          background: #ebecec;
        }
      }
    }
    .hots {
      h3 {
        font-size: 12px;
        padding: 5px 0;
      }
      p {
        font-size: 12px;
        margin: 8px 0;
      }
      ul.hotslist {
        width: 100%;
        li {
          display: inline-block;
          height: 32px;
          margin-right: 8px;
          margin-bottom: 8px;
          padding: 0 14px;
          font-size: 14px;
          line-height: 32px;
          border: 1px solid rgba(235, 235, 235, 0.7);
          border-radius: 20px;
        }
      }

      ul.history {
        margin-top: 20px;
        height: calc(31px * 5);
        overflow: hidden;
        li {
          line-height: 20px;
          padding: 5px 0;
          border-bottom: 1px solid rgba(235, 235, 235, 0.7);
          display: flex;
          align-items: center;
          span {
            width: 80%;
          }
          i {
            width: 10%;
            color: #c9c9ca;
          }
        }
      }
    }
    .suggestions {
      h3 {
        color: #507daf;
        height: 30px;
        line-height: 30px;
        border-bottom: 1px solid rgba(235, 235, 235, 0.7);
      }
      li {
        height: 40px;
        line-height: 40px;
        border-bottom: 1px solid rgba(235, 235, 235, 0.7);
        i {
          margin-right: 10px;
        }
      }
    }
    .suggestdata {
      p {
        margin-top: 20px;
        text-align: center;
      }
    }
  }
}
</style>
