<template>
  <div class="set">
    <div class="gundong">
      <input
        type="range"
        :max="duration"
        step="0.1"
        v-model="value"
        @input="pageInput"
        @change="pageChange"
      />
      <span
        class="tiao"
        :style="{ width: (value / duration) * 100 + '%' }"
      ></span>
    </div>

    <ul class="state">
      <li @click="getswitch">
        <i :class="[start[i]]"></i>
      </li>
      <li @click="$emit('play-page-back')">
        <i class="fa fa-fast-backward"></i>
      </li>
      <li @click="$emit('play-main-start')">
        <i
          :class="[playstart ? 'fa fa-pause-circle' : 'fa fa-play-circle']"
        ></i>
      </li>
      <li @click="$emit('play-page-next')">
        <i class="fa fa-fast-forward"></i>
      </li>
      <li @click.stop="$emit('get-showList', true)">
        <i class="fa fa-list-ul"></i>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    playstart: { type: Boolean },
    currentTime: { type: Number },
    duration: { type: Number },
  },
  data: function () {
    return {
      value: this.currentTime,
      inputIng: false,
      start: ["fa fa-exchange-alt", "fa fa-random", "fa fa-retweet"],
      i: 0,
    };
  },
  watch: {
    currentTime: function (n) {
      if (!this.inputIng) {
        this.value = n;
      }
    },
  },
  methods: {
    pageInput: function () {
      this.inputIng = true;
    },
    pageChange: function (event) {
      this.inputIng = false;
      this.$emit("play-page-change", event.target.value);
    },
    getswitch: function () {
      this.i++;
      this.i > 2 ? (this.i = 0) : this.i;
      this.start.forEach((item, index) => {
        if (this.i === index) {
          this.$emit("play-page-model", item);
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
i {
  color: white;
  font-size: 26px;
}
.gundong {
  margin: 20px auto;
  position: relative;
  width: 80vw;
  height: 6px;
  border-radius: 10px;
  background-color: white;
  box-shadow: 0 0 0 8px rgba(95, 95, 95, 0.2);
  input {
    width: 100%;
    position: absolute;
    top: 0;
    height: 100%;
    opacity: 0;
    z-index: 2;
  }
  .tiao {
    position: absolute;
    height: 100%;
    background-color: rgb(214, 52, 52);
    border-radius: inherit;
    &::before {
      content: "";
      display: block;
      width: 12px;
      height: 12px;
      position: absolute;
      background-color: rgb(170, 170, 170);
      top: -3px;
      right: 0;
      margin-right: -8px;
      border-radius: 50%;
    }
  }
}
ul.state {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  margin: 50px 0 60px;
  li {
    display: flex;
    background-color: rgba(124, 124, 124, 0.3);
    border-radius: 50%;
    width: 40px;
    height: 40px;
    margin: 0 10px;
    align-items: center;
    justify-content: center;
  }
}
</style>