<template>
  <section
    class="main"
    @click="$emit('get-showPlayPage', true)"
    v-if="songItem.song"
  >
    <div class="main-left" :class="{ playstart: playstart }">
      <img class="tou" :src="songItem.picUrl" />
    </div>
    <div class="ar-title">
      {{ songItem.name }} -
      <span>{{ songItem.song.artists[0].name }}</span>
    </div>
    <div class="main-right">
      <div class="click" @click.stop="$emit('play-main-start')">
        <canvas ref="canvas" width="40px" height="40px"> </canvas>
        <div class="icon" :class="[playstart ? 'pause' : 'play']"></div>
      </div>
      <div class="dedent" @click.stop="$emit('get-showList', true)">
        <i class="fas fa-list-ul"></i>
      </div>
    </div>
  </section>

  <section
    class="main"
    @click="$emit('get-showPlayPage', true)"
    v-else-if="songItem.artists"
  >
    <div class="main-left" :class="{ playstart: playstart }">
      <img class="tou" :src="SongPic" />
    </div>
    <div class="ar-title">
      {{ songItem.name }} -
      <span>{{ songItem.artists[0].name }}</span>
    </div>
    <div class="main-right">
      <div class="click" @click.stop="$emit('play-main-start')">
        <canvas ref="canvas" width="40px" height="40px"> </canvas>
        <div class="icon" :class="[playstart ? 'pause' : 'play']"></div>
      </div>
      <div class="dedent" @click.stop="$emit('get-showList', true)">
        <i class="fas fa-list-ul"></i>
      </div>
    </div>
  </section>

  <section class="main" @click="$emit('get-showPlayPage', true)" v-else>
    <div class="main-left" :class="{ playstart: playstart }">
      <img class="tou" :src="songItem.al.picUrl" />
    </div>
    <div class="ar-title">
      {{ songItem.name }} -
      <span>{{ songItem.ar[0].name }}</span>
    </div>
    <div class="main-right">
      <div class="click" @click.stop="$emit('play-main-start')">
        <canvas ref="canvas" width="40px" height="40px"> </canvas>
        <div class="icon" :class="[playstart ? 'pause' : 'play']"></div>
      </div>
      <div class="dedent" @click.stop="$emit('get-showList', true)">
        <i class="fas fa-list-ul"></i>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: {
    songItem: { type: Object },
    playstart: { type: Boolean },
    currentTime: { type: Number },
    duration: { type: Number },
    playlist: { type: Array },
    SongPic: { type: String },
  },
  computed: {
    averageTime: function () {
      return this.currentTime / this.duration;
    },
  },
  watch: {
    averageTime: function (n) {
      //   console.log(n);
      var context = this.$refs.canvas.getContext("2d");
      context.lineWidth = 2;

      context.clearRect(0, 0, 40, 40);
      context.beginPath();
      context.arc(
        20,
        20,
        18,
        (Math.PI / 180) * (0 - 90),
        (Math.PI / 180) * (360 - 90)
      );
      context.strokeStyle = "rgba(150, 150, 150, 0.3)";
      context.stroke();

      context.beginPath();
      context.arc(
        20,
        20,
        18,
        (Math.PI / 180) * (0 - 90),
        (Math.PI / 180) * (360 * n - 90)
      );
      context.strokeStyle = "#d43c33";
      context.stroke();
    },
  },
};
</script>

<style lang="less" scoped>
.main {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 50px;
  background: white;
  box-shadow: 0 0 2px 0 rgb(43, 43, 43);
  padding: 0 12px;
  .main-left {
    height: 30px;
    width: 30px;
    border-radius: 100%;
    box-shadow: 0 0 5px 0 rgb(43, 43, 43);
    overflow: hidden;
    animation: fang 1s linear 0s forwards;
    animation-play-state: paused;
    img.tou {
      width: 100%;
      height: 100%;
      animation: rotateImg 5s linear 0s infinite;
      animation-play-state: paused;
    }
    .ar-title {
      display: flex;
    }
    &.playstart {
      animation-play-state: running;
      .tou {
        animation-play-state: running;
      }
    }
  }
  .ar-title {
    width: 55%;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    font-size: 14px;
    span {
      font-size: 12px;
      color: #888;
    }
  }
  .main-right {
    display: flex;
    align-items: center;
    .click {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 40px;
      canvas {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
      }
      .icon {
        width: 12px;
        height: 12px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        &.play {
          border-top: 6px solid transparent;
          border-bottom: 6px solid transparent;
          border-left: 10px solid rgb(0, 0, 0);
          transform: translateX(3px);
        }
        &.pause {
          &::before,
          &::after {
            content: "";
            display: block;
            height: 80%;
            width: 20%;
            background: black;
          }
        }
      }
    }
    .dedent {
      margin-left: 10px;
      i {
        font-size: 20px;
        color: black;
      }
    }
  }
}

@keyframes fang {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.2);
  }
}
</style>