<template>
  <div id="app">
    <header class="top" v-if="$route.meta.showNav">
      <div class="logo">
        <div class="topLeft">心云音乐</div>
      </div>
      <!-- 导航条 -->
      <ul id="nav">
        <li><router-link to="/">推荐音乐</router-link></li>
        <li><router-link to="/hot">热歌榜</router-link></li>
        <li><router-link to="/search">搜索</router-link></li>
      </ul>
    </header>

    <section class="router">
      <transition
        name="router-transition"
        enter-active-class=" animate__animated animate__fadeInRight"
        leave-active-class="animate__animated animate__fadeOutLeft"
      >
        <keep-alive>
          <router-view
            class="routers"
            :cardlist="cardlist"
            :playstart="playstart"
            @get-song-list="getSongId"
            @get-home-play="getPlayList"
            :songItemId="songItem ? songItem.id : null"
            @get-home-card="getCardList"
          />
        </keep-alive>
      </transition>
    </section>

    <audio
      style="height: 50px; display: none"
      ref="audio"
      :src="getSongUrl"
      controls
      autoplay
      @playing="playing"
      @pause="pause"
      @timeupdate="timeupdate"
      @durationchange="durationchange"
    ></audio>

    <Player
      v-if="songItem"
      :songItem="songItem"
      :playstart="playstart"
      :currentTime="currentTime"
      :duration="duration"
      :playlist="playlist"
      :songItemId="songItem ? songItem.id : null"
      @play-main-start="getPlayStart"
      @get-song-list="getSongId"
      @play-page-back="playPageBack"
      @play-page-next="playPageNext"
      @play-page-change="$refs.audio.currentTime = $event"
      @play-page-model="getModel"
    ></Player>
  </div>
</template>

<script>
import Player from "@/components/Player.vue";
export default {
  components: { Player },

  data: function () {
    return {
      songItem: null,
      playstart: false,
      currentTime: 0,
      duration: 0,
      playlist: [],
      cardlist: {},
      musicIndex: 0,
      fa: "fa fa-exchange-alt",
      musicArr: [],
    };
  },

  computed: {
    getSongUrl: function () {
      if (this.songItem) {
        return `https://music.163.com/song/media/outer/url?id=${this.songItem.id}.mp3 `;
      } else {
        return null;
      }
    },
  },

  methods: {
    getSongId: function (value) {
      this.songItem = value;
    },
    getPlayList: function (value) {
      this.playlist = value;
    },
    getCardList: function (value) {
      this.cardlist = value;
    },
    getPlayStart: function () {
      if (this.playstart) {
        this.$refs.audio.pause();
      } else {
        this.$refs.audio.play();
      }
    },
    playPageBack: function () {
      let index = this.playlist.findIndex((item) => {
        return item.id === this.songItem.id;
      });
      index--;
      index = index < 0 ? this.playlist.length - 1 : index;
      this.getSongId(this.playlist[index]);
    },
    playPageNext: function () {
      let index = this.playlist.findIndex((item) => {
        return item.id === this.songItem.id;
      });
      index++;
      index = index > this.playlist.length - 1 ? 0 : index;
      this.getSongId(this.playlist[index]);
    },
    timeupdate: function () {
      this.currentTime = this.$refs.audio.currentTime;
    },
    durationchange: function () {
      this.duration = this.$refs.audio.duration;
    },
    getModel: function (value) {
      this.fa = value;
    },
    orderPlaying: function () {
      this.musicIndex = this.playlist.findIndex(
        (item) => item.id === this.songItem.id
      );
      this.musicIndex++;
      this.musicIndex = this.musicIndex >= 10 ? 0 : this.musicIndex;
      this.getSongId(this.playlist[this.musicIndex]);
    },
    randomPlaying: function () {
      this.getSongId(this.playlist[this.musicArr[0]]);
    },
    cyclePlaying: function () {
      this.playlist.filter((item, index) => {
        if (item.id === this.songItem.id) {
          this.getSongId(this.playlist[index]);
        }
      });
      this.$refs.audio.play();
    },
    playing: function () {
      this.playstart = true;
    },
    pause: function () {
      this.playstart = false;
    },
  },
  watch: {
    currentTime: function (n) {
      if (this.duration === n && this.fa === "fa fa-exchange-alt") {
        this.orderPlaying();
      } else if (this.duration === n && this.fa === "fa fa-random") {
        this.randomPlaying();
      } else if (this.duration === n && this.fa === "fa fa-retweet") {
        this.cyclePlaying();
      }
    },
    playlist: function (n) {
      if (n) {
        this.musicArr = [];
        this.playlist.forEach((item, index) => {
          this.musicArr.push(index);
          this.musicArr.sort(function () {
            return Math.random() - 0.5;
          });
        });
        this.musicArr = [...new Set(this.musicArr)];
      }
    },
  },
};
</script>

