<template>
  <li class="song-list" @click="$emit('get-song-list', item)" v-if="item.song">
    <div class="lis-left">
      <div class="lis-top lis">
        {{ item.name }}
        <span
          class="last-name"
          v-for="(n, index) in item.song.alias"
          :key="index"
          >{{ n }}</span
        >
      </div>
      <div class="lis-bottom lis">
        <span class="sq" v-if="item.song.hMusic.sr === 48000"></span>
        <i class="artists" v-for="(artist, i) in item.song.artists" :key="i">
          {{ artist.name }}
        </i>
        <span>{{ item.song.album.name }}</span>
      </div>
    </div>
    <div class="lis-right">
      <div
        class="play"
        :class="{ playing: songItemId === item.id, playstart: playstart }"
      >
        <div class="flan"></div>
        <div class="flan"></div>
        <div class="flan"></div>
      </div>
    </div>
  </li>

  <li
    class="song-list"
    @click="$emit('get-song-list', item)"
    v-else-if="item.artists"
  >
    <div class="lis-left">
      <div class="lis-top lis">
        {{ item.name }}
        <span class="last-name" v-for="(n, index) in item.alias" :key="index">{{
          n
        }}</span>
      </div>
      <div class="lis-bottom lis">
        <span class="sq" v-if="item.copyrightId"></span>

        <i class="artists" v-for="(artist, i) in item.artists" :key="i">
          {{ artist.name }}
        </i>
        <span>{{ item.album.name }}</span>
      </div>
    </div>
    <div class="lis-right">
      <div
        class="play"
        :class="{ playing: songItemId === item.id, playstart: playstart }"
      >
        <div class="flan"></div>
        <div class="flan"></div>
        <div class="flan"></div>
      </div>
    </div>
  </li>

  <li class="song-list" @click="$emit('get-song-list', item)" v-else>
    <div class="xu"><slot></slot></div>
    <div class="lis-left">
      <div class="lis-top lis">
        {{ item.name }}
        <span class="last-name" v-for="(n, index) in item.alia" :key="index">{{
          n
        }}</span>
      </div>
      <div class="lis-bottom lis">
        <span class="sq" v-if="item.no < 7"></span>

        <i class="artists" v-for="(artist, i) in item.ar" :key="i">
          {{ artist.name }}
        </i>
        <span>{{ item.al.name }}</span>
      </div>
    </div>
    <div class="lis-right">
      <div
        class="play"
        :class="{ playing: songItemId === item.id, playstart: playstart }"
      >
        <div class="flan"></div>
        <div class="flan"></div>
        <div class="flan"></div>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
    songItemId: {
      type: Number,
    },
    playstart: {
      type: Boolean,
    },
    suggestData: {
      type: Array,
    },
  },
};
</script>

<style lang="less" scoped>
.song-list {
  display: flex;
  padding: 5px 12px 6px 0;
  margin-left: 12px;
  align-items: center;
  border-bottom: 1px solid rgba(235, 235, 235, 0.5);
  .xu {
    // margin-right: 12px;
    width: 8%;
    color: #999;
  }
  &.cl {
    .xu {
      color: #d43c33;
    }
  }
  .lis-left {
    flex: 1;
    margin-right: 15px;
    .lis {
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
    .lis-top {
      .last-name {
        color: #888;
        &::before {
          content: "(";
        }
        &::after {
          content: ")";
        }
      }
    }
    .lis-bottom {
      font-size: 12px;
      color: #888;
      .sq {
        background: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=")
          no-repeat;
        background-size: 166px 97px;
        display: inline-block;
        width: 12px;
        height: 8px;
        margin-right: 4px;
      }
      i.artists {
        font-style: normal;
        &::after {
          content: "/";
        }
        &:last-of-type {
          &::after {
            content: "- ";
          }
        }
      }
    }
  }
  .lis-right {
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    .play {
      width: 100%;
      height: 100%;
      background: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=")
        no-repeat;
      background-size: 166px 97px;
      display: inline-block;
      background-position: -24px 0;
    }
    .playing {
      background: none;
      display: flex;
      justify-content: space-around;
      .flan {
        width: 4px;
        height: 100%;
        background: red;
        transform-origin: bottom;
        animation: playing 0.7s linear 0s infinite alternate;
        animation-play-state: paused;
        &:nth-child(1) {
          animation-delay: -0.4s;
        }
        &:nth-child(2) {
          animation-delay: -0.8s;
        }
      }
      &.playstart {
        .flan {
          animation-play-state: running;
        }
      }
    }
  }
}
@keyframes playing {
  from {
    transform: scaleY(0.2);
  }
  to {
    transform: scaleY(0.7);
  }
}
</style>