<template>
  <li
    @click="
      $router.push('/playlist?id=' + item.id);
      $emit('get-home-card-list', item);
    "
    class="card-list"
    v-if="item.picUrl"
  >
    <div class="card">
      <img class="bg" :src="`${item.picUrl}?param=200y200`" />
      <div class="tiao">
        <img
          src="data:image/svg+xml;base64,"
        />
        <span>{{ item.playCount | getPlayCount }}</span>
      </div>
    </div>
    <p>{{ item.name }}</p>
  </li>

  <ul v-else class="card-ul">
    <div
      class="card-mark"
      :style="{
        backgroundImage: `url(${item.coverImgUrl}?imageView=1&type=webp&thumbnail=360x0)`,
      }"
    ></div>
    <li class="card-playlist">
      <div class="imgs">
        <img
          :src="`${item.coverImgUrl}?imageView=1&type=webp&thumbnail=360x0`"
        />
        <p class="ge">歌单</p>
        <div class="card-img">
          <img
            src="data:image/svg+xml;base64,"
          />
          <span>{{ item.playCount | getPlayCount }}</span>
        </div>
      </div>
      <div class="title">
        <p>{{ item.name }}</p>
        <div class="tou">
          <img :src="`${item.creator.avatarUrl}`" /><span>{{
            item.creator.nickname
          }}</span>
        </div>
      </div>
    </li>
  </ul>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  filters: {
    getPlayCount: function (value) {
      if (value > 100000000) {
        let num1 = value / 100000000;
        let num1arr = num1.toString().split("");
        let index1 = num1arr.indexOf(".");
        if (num1arr[index1 + 1] == 0) {
          return parseInt(value / 10000000) / 10 + "亿";
        } else {
          return (value / 100000000).toFixed(1) + "亿";
        }
      } else if (value > 100000) {
        let num2 = value / 10000;
        let num2arr = num2.toString().split("");
        let index2 = num2arr.indexOf(".");
        if (num2arr[index2 + 1] == 0) {
          return parseInt(value / 1000) / 10 + "万";
        } else {
          return (value / 10000).toFixed(1) + "万";
        }
      } else {
        return value;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.card-list {
  display: flex;
  width: 33%;
  flex-direction: column;
  .card {
    position: relative;
    height: 73%;
    img.bg {
      vertical-align: middle;
    }
    .tiao {
      position: absolute;
      top: 0;
      right: 0;
      width: 100%;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      background-image: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0.6),
        rgba(0, 0, 0, 0.1)
      );

      img {
        width: 12px;
        height: 12px;
        margin-right: 5px;
      }
      span {
        font-size: 13px;
        color: white;
        margin-right: 3px;
      }
    }
  }
  p {
    font-size: 13px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    padding: 6px 2px 0 6px;
    margin-bottom: 10px;
  }
}
.card-ul {
  width: 100%;
  height: 186px;
  overflow: hidden;
  position: relative;
  background-color: rgba(0, 0, 0, 0.25);
  .card-mark {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    transform: scale(1.5);
    filter: blur(30px);
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
  .card-playlist {
    display: flex;
    justify-content: space-between;
    padding: 30px 15px;
    .imgs {
      width: 126px;
      height: 126px;
      position: relative;
      .ge {
        position: absolute;
        left: 0;
        top: 13px;
        padding: 0 8px;
        height: 17px;
        color: #fff;
        font-size: 12px;
        text-align: center;
        line-height: 17px;
        background-color: rgba(217, 48, 48, 0.8);
        border-top-right-radius: 17px;
        border-bottom-right-radius: 17px;
      }
      .card-img {
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        background-image: linear-gradient(
          180deg,
          rgba(0, 0, 0, 0.6),
          rgba(0, 0, 0, 0.1)
        );
        img {
          width: 12px;
          height: 12px;
          margin-right: 5px;
        }
        span {
          font-size: 13px;
          color: white;
          margin-right: 3px;
        }
      }
    }
    .title {
      width: 55%;
      color: white;
      .tou {
        display: flex;
        align-items: center;
        margin-top: 5px;
        img {
          width: 30px;
          height: 30px;
          border-radius: 50%;
          margin-right: 5px;
        }
        span {
          width: 60%;
          font-size: 14px;
          color: rgb(199, 199, 199);
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
      }
    }
  }
}
</style>