<template>
  <section class="player">
    <transition
      name="playmain-transition"
      enter-active-class="animate__animated animate__slideInUp "
      leave-active-class="animate__animated animate__fadeOutDown"
    >
      <PlayMain
        v-if="!showPlayPage"
        :songItem="songItem"
        :playstart="playstart"
        :playlist="playlist"
        :currentTime="currentTime"
        :duration="duration"
        :songItemId="songItemId"
        :SongPic="SongPic"
        @play-main-start="$emit('play-main-start')"
        @get-showList="showList = $event"
        @get-showPlayPage="showPlayPage = $event"
      ></PlayMain>
    </transition>
    <transition
      name="playpagr-transition"
      enter-active-class=" animate__animated animate__slideInUp"
      leave-active-class="animate__animated animate__fadeOutDown"
    >
      <PlayPage
        v-if="showPlayPage"
        :songItem="songItem"
        :playstart="playstart"
        :currentTime="currentTime"
        :duration="duration"
        :songItemId="songItemId"
        :SongPic="SongPic"
        @get-showPlayPage="showPlayPage = $event"
        @play-main-start="$emit('play-main-start')"
        @get-showList="showList = $event"
        @play-page-model="$emit('play-page-model', $event)"
        @play-page-back="$emit('play-page-back')"
        @play-page-next="$emit('play-page-next')"
        @play-page-change="$emit('play-page-change', $event)"
      ></PlayPage>
    </transition>
    <MusicList
      v-if="showList"
      :songItem="songItem"
      :playstart="playstart"
      :playlist="playlist"
      @get-song-list="$emit('get-song-list', $event)"
      @get-showList="showList = $event"
    ></MusicList>
  </section>
</template>

<script>
import PlayMain from "@/components/PlayMain.vue";
import MusicList from "@/components/MusicList.vue";
import PlayPage from "@/components/PlayPage.vue";

export default {
  components: { PlayMain, MusicList, PlayPage },
  props: {
    songItem: { type: Object },
    playstart: { type: Boolean },
    currentTime: { type: Number },
    duration: { type: Number },
    playlist: { type: Array },
    songItemId: { type: Number },
  },
  data: function () {
    return {
      showList: false,
      showPlayPage: false,
      SongPic:
        "https://p2.music.126.net/6y-UleORITEDbvrOLV0Q8A==/5639395138885805.jpg",
    };
  },
  methods: {
    getPicUrl: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/song/detail", {
          params: {
            ids: id,
          },
        })
        .then((res) => {
          this.SongPic = res.data.songs[0].al.picUrl;
        });
    },
  },
  watch: {
    songItemId: function (n) {
      this.getPicUrl(n);
    },
  },
  created: function () {
    this.getPicUrl(this.songItemId);
  },
};
</script>

<style lang="less" scoped>
.player {
  height: 50px;
  position: relative;
  top: 0;
  left: 0;
}
</style>