<template>
  <section class="home">
    <div class="homeSon">
      <HomeTitle>编辑推荐</HomeTitle>
      <ul class="home-cardlist">
        <CardList
          v-for="item in personalizeds"
          :key="item.id"
          :item="item"
          @get-home-card-list="$emit('get-home-card', $event)"
        ></CardList>
      </ul>
      <HomeTitle>最新音乐</HomeTitle>
      <ul class="home-songlist">
        <SongList
          v-for="(item, index) in newsongs"
          :key="item.id"
          :item="item"
          :songItemId="songItemId"
          :playstart="playstart"
          @get-song-list="
            $emit('get-song-list', $event);
            $emit('get-home-play', newsongs);
          "
          >{{ (index + 1).toString().padStart(2, "0") }}</SongList
        >
      </ul>
    </div>
  </section>
</template>

<script>
import HomeTitle from "@/components/HomeTitle.vue";
import CardList from "@/components/CardList.vue";
import SongList from "@/components/SongList.vue";

export default {
  components: { HomeTitle, CardList, SongList },
  props: {
    songItemId: {
      type: Number,
    },
    playstart: {
      type: Boolean,
    },
  },
  data: function () {
    return {
      personalizeds: [],
      newsongs: [],
    };
  },

  created: function () {
    this.axios
      .get("https://apis.netstart.cn/music/personalized?limit=6")
      .then((res) => {
        // console.log(res);
        this.personalizeds = res.data.result;
      });

    this.axios
      .get("https://apis.netstart.cn/music/personalized/newsong")
      .then((res) => {
        // console.log(res.data.result);
        this.newsongs = res.data.result;
      });
  },
};
</script>
<style lang="less" scoped>
.home {
  height: calc(100vh - 128px);
  overflow: hidden;
  .homeSon {
    position: absolute;
    height: 100%;
    overflow-y: auto;
    width: 100%;
    .home-cardlist {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }
}
</style>