<template>
  <section class="lyric animate__animated animate__fadeInUp">
    <div class="lyric-main" ref="lyric">
      <ul ref="lis" :style="{ marginTop: top + 'px' }" v-if="lyrics.length">
        <li v-for="(item, index) in lyrics" :key="index">
          <span
            :style="{
              animationDuration: lyrics[index + 1]
                ? lyrics[index + 1].time - item.time - 0.5 + 's'
                : '3s',
            }"
            :class="{ active: currentIndex === index, playstart: playstart }"
            >{{ item.text }}</span
          >
        </li>
      </ul>
      <ul v-else>
        <li>没有找到歌词</li>
      </ul>
    </div>
    <PlayPageSet
      :playstart="playstart"
      :currentTime="currentTime"
      :duration="duration"
      @get-showList="$emit('get-showList', $event)"
      @play-page-model="$emit('play-page-model', $event)"
      @play-page-back="$emit('play-page-back')"
      @play-main-start="$emit('play-main-start')"
      @play-page-next="$emit('play-page-next')"
      @play-page-change="$emit('play-page-change', $event)"
    ></PlayPageSet>
  </section>
</template>

<script>
import PlayPageSet from "@/components/PlayPageSet.vue";

export default {
  components: { PlayPageSet },

  props: {
    songItemId: { type: Number },
    currentTime: { type: Number },
    playstart: { type: Boolean },
    duration: { type: Number },
  },
  data: function () {
    return {
      lyric: null,
      top: 0,
    };
  },
  created: function () {
    this.getLyric(this.songItemId);
  },
  methods: {
    getLyric: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/lyric", {
          params: { id },
        })
        .then((res) => {
          this.lyric = res.data.lrc.lyric;
        });
    },
  },
  computed: {
    lyrics: function () {
      if (this.lyric) {
        return this.lyric
          .split("\n")
          .filter((item) => item)
          .map((item) => {
            let text = item.replace(/^\[\d{2}:\d{2}\.\d{2}\d?\]/g, "");
            let b = item.replace(text, "").replace("[", "").replace("]", "");
            let bArr = b.split(":").map((item) => Number(item));
            let time = bArr[0] * 60 + bArr[1];
            return { text, time };
          });
      } else {
        return [];
      }
    },
    currentIndex: function () {
      let index = this.lyrics.findIndex((item) => item.time > this.currentTime);
      let currentIndex = index !== -1 ? index - 1 : this.lyrics.length - 1;
      return currentIndex;
    },
  },
  watch: {
    songItemId: function (n) {
      this.getLyric(n);
    },
    currentIndex: function (n) {
      this.$nextTick(() => {
        let lis = this.$refs.lis.querySelectorAll("li");
        let top = [...lis]
          .map((item) => item.offsetHeight)
          .slice(0, n)
          .reduce(function (total, item) {
            return total + item;
          }, 0);
        let lyricH = this.$refs.lyric.offsetHeight;
        // // console.log(lyricH);
        top = top > lyricH / 2 ? top - lyricH / 2 : 0;
        // console.log(top - lyricH * 1.5);
        // // top = top > top - lyricH ? top - lyricH : top;
        this.top = -top;
      });
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes textColor {
  from {
    background-position-x: 0;
  }
  to {
    background-position-x: -100%;
  }
}
.lyric {
  width: 100%;
  height: 88%;
  background-color: rgba(0, 0, 0, 0.5);
  .lyric-main {
    overflow: hidden;
    height: 75%;
    box-shadow: 0 0 5px 0 rgb(43, 43, 43);
    ul {
      transition: all 0.3s linear;
      li {
        text-align: center;
        line-height: 44px;
        color: white;
        padding: 0 35px;
        span {
          &.active {
            background-image: linear-gradient(
              to right,
              #fd8403 50%,
              rgb(255, 252, 67) 50%
            );
            background-size: 200%;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            animation-name: textColor;
            animation-duration: 3s;
            animation-timing-function: linear;
            animation-play-state: paused;
            &.playstart {
              animation-play-state: running;
            }
          }
        }
      }
    }
  }
}
</style>