<template>
  <section class="playlist" v-if="detail">
    <CardList :item="detail"></CardList>
    <h3 class="playlist-title">歌曲列表</h3>
    <ul>
      <SongList
        v-for="(item, index) in detail.tracks"
        :key="item.id"
        :item="item"
        :songItemId="songItemId"
        :playstart="playstart"
        :class="{ cl: index < 3 }"
        @get-song-list="
          $emit('get-song-list', item);
          $emit('get-home-play', detail.tracks);
        "
        >{{ index + 1 }}</SongList
      >
      <!-- <li v-for="item in detail.tracks" :key="item.id">{{ item.name }}</li> -->
    </ul>
  </section>
</template>

<script>
import SongList from "@/components/SongList.vue";
import CardList from "@/components/CardList.vue";

export default {
  components: { SongList, CardList },
  props: {
    songItemId: {
      type: Number,
    },
    playstart: {
      type: Boolean,
    },
  },
  data: function () {
    return {
      detail: null,
    };
  },
  methods: {
    getPlayListDetail: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/playlist/detail", {
          params: {
            id,
          },
        })
        .then((res) => {
          //   console.log(res.data.playlist);
          this.detail = res.data.playlist;
        });
    },
  },
  // beforeRouteUpdate(to, from, next) {
  //   this.gender = to.query.gender || "male";
  //   next();
  // },
  created: function () {
    // console.log(this.$route.query.id);
    this.getPlayListDetail(this.$route.query.id);
  },
  watch: {
    songItemId: function (n) {
      this.getPlayListDetail(n);
    },
  },
};
</script>

<style lang="less" scoped>
.playlist {
  .playlist-title {
    height: 23px;
    font-size: 12px;
    line-height: 23px;
    padding: 0 10px;
    background-color: #eeeff0;
  }
}
</style>